@import "../../scss/variables.scss";
@import "../../scss/mixin.scss";

.colorPicker {
  @include flexbox(space-between, center);
  border-radius: 2px;
  margin: 1rem 0;
  padding: 0.5rem;

  .color__picker_field {
    @include flexbox(space-between, center);
  }

  .color__code {
    font-weight: bold;
    font-style: italic;
  }

  .pick__icon {
    width: 2rem;
    height: 2rem;
    margin-left: 0.8rem;
    color: gray;
  }

  input {
    width: 60px;
    height: 45px;
    border: none;
    background-color: transparent;
    padding-right: 6px;
    outline: none;
    margin-left: 1rem;
  }
}
