@import "../../scss/variables.scss";

.modal {
  background-color: white;
  width: 90vw;
  min-height: 25vh;
  border-radius: 2px;
  position: fixed;
  top: 45%;
  left: 50%;
  z-index: 2000;
  transform: translate(-50%, -50%);
  padding: 1rem;
  transition: all 1s;

  h3 {
    font-weight: normal;
    color: $secondaryColor;
    padding-bottom: 8px;
  }

  p {
    padding-bottom: 12px;
    color: $color-four;
  }

  .controlBtn {
    text-align: right;
  }

  .errorMsg {
    color: red;
    font-size: 0.8em;
    padding-bottom: 8px;
  }
}

@media screen and (min-width: 600px) {
  .modal {
    width: 60vw;
  }
}

@media screen and (min-width: 845px) {
  .modal {
    width: 40vw;
  }
}

@media screen and (min-width: 1100px) {
  .modal {
    width: 35vw;
  }
}
