@import "../../scss/variables.scss";
@import "../../scss/mixin.scss";

.menubar {
  width: 75vw;
  min-height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2000;
  background-color: white;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  transition: all 1s;

  .menubar__heading {
    @include flexbox(space-between, center);
    margin-bottom: 1rem;
  }

  .cancel__icon {
    width: 1.5rem;
    height: 1.5rem;
  }

  .link {
    padding: 0.8rem 0;
    color: $color-four;

    &:hover {
      color: $secondaryColor;
    }
  }

  .link--active {
    color: $primaryColor;
  }
}

@media screen and (min-width: 600px) {
  .menubar {
    width: 35vw;
  }
}

@media screen and (min-width: 750px) {
  .menubar {
    width: 30vw;
  }
}

@media screen and (min-width: 850px) {
  .menubar {
    width: 25vw;
  }
}
