@import "../../scss/variables.scss";
@import "../../scss/mixin.scss";

.collection {
  padding: 1rem;
  min-height: 70vh;
  text-align: center;

  @include heading;

  .collection__container {
    margin: 1rem 0;
  }

  .palettes {
    transition: all 1s;
    .palette {
      width: 100%;
      height: 190px;
      box-shadow: 0 0 0.3rem 0 $color-four;
      border-radius: 0.1rem;
      background-color: white;
      position: relative;
      overflow: hidden;
      margin: 1rem 0;

      .colors {
        width: 100%;
        height: 80%;
        display: flex;
        overflow: hidden;

        .color {
          width: 100%;
          height: 100%;
          position: relative;

          &:hover {
            width: 200%;
            transition: all 0.3s ease-out;
          }

          .colorCode {
            width: 100%;
            position: absolute;
            bottom: 0;
            padding: 0.7rem;
            font-weight: bold;
            opacity: 0;
            display: block;
            text-align: center;
            text-transform: uppercase;
            background-color: rgba(255, 255, 255, 0.3);
          }

          &:hover .colorCode {
            opacity: 1;
            cursor: pointer;
          }
        }
      }

      .trashIcon {
        width: 1.8rem;
        height: 1.8rem;
        position: absolute;
        background-color: #ffffff4d;
        top: 0;
        right: 0;
        color: $color-three;
        padding: 0.3rem;
        font-size: 1.5em;
        border-radius: 2px;
        cursor: pointer;

        &:active {
          color: red;
        }
      }
    }

    h4 {
      width: 100%;
      height: 20%;
      font-weight: bold;
      text-transform: capitalize;
      padding-top: 0.5rem;
      color: $secondaryColor;
    }
  }

  .message {
    color: gray;
    font-style: italic;
    padding: 100px 0;
    text-align: center;
  }
}

@media screen and (min-width: 500px) {
  .palettes {
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 49%);
    gap: 0 2%;
  }
}

@media screen and (min-width: 800px) {
  .palettes {
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 32%);
    gap: 0 2%;
  }
}

@media screen and (min-width: 1200px) {
  .palettes {
    margin: auto;
    display: grid;
    grid-template-columns: repeat(4, 24%);
    gap: 0 1%;
  }
}
