@import "./variables.scss";

@mixin heading {
  h2 {
    font-size: 1.6rem;
    color: $primaryColor;
    padding-bottom: 0.4rem;
  }

  p {
    font-size: 1.2rem;
    color: $color-four;
  }
}

@mixin flexbox($hor, $ver) {
  display: flex;
  justify-content: $hor;
  align-items: $ver;
}
