@import "../../scss/variables.scss";
@import "../../scss/mixin.scss";

.navbar {
  @include flexbox(space-between, center);
  padding: 1rem;
  background-color: white;

  .logo__title {
    @include flexbox(center, center);
  }

  h3 {
    cursor: pointer;
  }

  .menu__icon {
    width: 1.5rem;
    height: 1.5rem;
    color: $color-four;
    margin-left: 2rem;
  }

  .logo {
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 0.6rem;
  }

  .nav__links {
    @include flexbox(space-between, center);
  }

  .my__palette__link {
    display: none;
    font-weight: bold;
  }
}

@media screen and (min-width: 650px) {
  .navbar {
    .my__palette__link {
      display: block;
    }
  }
}
