@import "../../scss/variables.scss";

.field {
  input {
    width: 100%;
    border: none;
    border-bottom: solid 2px #ccc;
    padding: 4px 2px;
    outline: none;
    font-size: 0.9em;
    margin-bottom: 12px;

    &:focus {
      border-bottom: solid 2px $color-three;
    }
  }
}
