@import "../../scss/variables.scss";
@import "../../scss//mixin.scss";

.terms {
  margin: auto;
  padding: 1rem 1.5rem;
  text-align: left;

  @include heading;

  .wrapper {
    margin-bottom: 1rem;
  }

  .list {
    width: 80%;
    margin: auto;

    li {
      padding: 4px 0;
      color: gray;
      font-size: 0.9em;
    }
  }
}

@media screen and (min-width: 1100px) {
  .terms {
    width: 60vw;
    margin: 0.5rem auto;
  }
}
