@import "../../scss/variables.scss";

.normalBtn,
.primaryBtn {
  background-color: white;
  color: $color-three;
  border: none;
  box-shadow: 0 0 0.3rem 0 $color-four;
  border-radius: 0.1rem;
  font-size: 0.9rem;
  font-weight: bold;
  margin: 0.6rem;
  padding: 0.8rem 2rem;
  outline: none;
  text-transform: capitalize;
}

.primaryBtn {
  background: $gradient;
  color: white;
}
