@import "../../scss/variables.scss";
@import "../../scss/mixin.scss";

.generate {
  min-height: 70vh;
  padding: 1rem;
  text-align: center;

  @include heading;

  .color__generator__container {
    transition: all 1s;
  }

  .new__palette {
    width: 100%;
    height: 180px;
    border: solid 0.5px #ccc;
    margin: 1rem 0;
    border-radius: 2px;
    overflow: hidden;
    display: flex;
    animation: slide 0.4s ease-out;
  }

  .createBtn {
    text-align: center;
    margin: 20px 0;
  }

  .message {
    color: gray;
    font-style: italic;
    padding: 35px 0;
    text-align: center;
  }
}

@keyframes slide {
  0% {
    transform: translateY(-300px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@media screen and (min-width: 500px) {
  .color__generator__container {
    width: 80vw;
    margin: auto;
  }
}

@media screen and (min-width: 1100px) {
  .color__generator__container {
    width: 60vw;
    margin: auto;
  }
}
