@import "../../scss/variables.scss";
@import "../../scss/mixin.scss";

.miniPalette {
  width: 100%;
  height: 240px;
  background-color: white;
  box-shadow: 0 0 0.1rem 0 $color-four;
  border-radius: 2px;
  overflow: hidden;
  margin: 1rem 0;

  .miniColorBoxs {
    display: grid;
    grid-template-columns: repeat(5, 20%);
  }

  h4 {
    color: $secondaryColor;
    font-weight: bold;
    padding-top: 0.6rem;
  }

  &:hover {
    transform: scale(1.01);
    transition: all 0.2s ease-in-out;
    box-shadow: 0 0 0.5rem 0 $color-four;
  }
}
