@import "../../scss/variables.scss";
@import "../../scss/mixin.scss";

.footer {
  background-color: $color-three;
  color: white;
  padding: 3rem 1rem;
  min-height: 30vh;

  p {
    color: $color-four;
  }

  .footer__social__media {
    @include flexbox(space-between, center);
    border-bottom: solid 0.1px rgb(70, 70, 70);
    padding-bottom: 1rem;
  }

  .copy__write {
    text-align: center;
    font-size: 0.75rem;
    color: $color-four;
    padding: 1rem 0;
  }

  .socialLinks {
    padding: 0 4px;

    .socialIcons {
      font-size: 1.3em;
      margin-left: 1rem;

      &:hover {
        transform: scale(1.1);
      }
    }
  }
}
