.colorBox {
  width: 20%;
  height: 25%;
  position: relative;

  .copyBtn,
  .colorCode {
    position: absolute;
    bottom: 10%;
    right: 3%;
    display: none;
  }

  .colorCode {
    top: 10%;
    left: 5%;
    text-transform: uppercase;
    display: block;
    font-size: 0.85em;
    font-weight: 600;
  }

  .copy__btn {
    padding: 0.4rem 1rem;
  }

  &:hover .copyBtn {
    display: block;
  }
}

@media (max-width: 800px) {
  .colorBox {
    width: 50%;
    height: 10%;
  }
}
